import React from "react";
import { animateScroll as scroll } from "react-scroll";
import "../styles/module.privacy.css";

function TermsOfService() {
  const [show, setShow] = React.useState(false);

  React.useEffect(() => {
    const handleScroll = () => {
      setShow(window.scrollY > 200);
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const scrollToTop = () => {
    scroll.scrollToTop({ duration: 300 });
  };

  return (
    <>
      {/* Hero Section */}
      <section className="hero maid-services">
        <div className="hero__content maid-services__content">
          <h1 className="hero__title maid-services__title">Terms of Service</h1>
          <p className="hero__subtitle maid-services__subtitle">
            At Aitemaad Maid Services, we value transparency and are committed to
            safeguarding your trust. Below, you will find the terms that govern
            your use of our services and platform.
          </p>
        </div>
      </section>

      {/* Terms of Service Section */}
      <section className="terms-of-service container">
        <h2 className="terms-of-service__title">Terms of Service</h2>

        <p className="terms-of-service__paragraph">
          Welcome to Aitemaad Maid Services! By using our platform, you agree to
          abide by the terms outlined here. Please review these carefully to
          understand your rights and responsibilities.
        </p>

        <h3 className="terms-of-service__subsection-title">1. Introduction</h3>
        <p className="terms-of-service__paragraph">
          These Terms of Service ("Terms") govern your use of Aitemaad Maid
          Services' website, applications, and all related services ("Services").
          By accessing or using our Services, you agree to these Terms and our
          Privacy Policy.
        </p>

        <h3 className="terms-of-service__subsection-title">2. Your Obligations</h3>
        <p className="terms-of-service__paragraph">
          When using our Services, you agree to:
        </p>
        <ul>
          <li>Provide accurate and up-to-date information when requested.</li>
          <li>
            Use our Services only for lawful purposes, refraining from
            activities that may harm or disrupt our platform.
          </li>
          <li>
            Avoid sharing malicious content, spam, or violating intellectual
            property rights.
          </li>
        </ul>

        <h3 className="terms-of-service__subsection-title">3. User Content</h3>
        <p className="terms-of-service__paragraph">
          You retain ownership of any content you upload, post, or share through
          our Services. However, by sharing content, you grant us a non-exclusive,
          royalty-free license to use, distribute, and display it as necessary to
          operate our Services.
        </p>

        <h3 className="terms-of-service__subsection-title">4. Intellectual Property</h3>
        <p className="terms-of-service__paragraph">
          All materials, trademarks, and intellectual property available through
          our Services are owned by Aitemaad Maid Services or our licensors. You
          may not reproduce, distribute, or modify these materials without our
          express written permission.
        </p>

        <h3 className="terms-of-service__subsection-title">5. Service Availability</h3>
        <p className="terms-of-service__paragraph">
          We strive to ensure uninterrupted access to our Services but cannot
          guarantee 100% availability. Scheduled maintenance, technical issues,
          or factors beyond our control may occasionally cause disruptions.
        </p>

        <h3 className="terms-of-service__subsection-title">6. Limitation of Liability</h3>
        <p className="terms-of-service__paragraph">
          To the fullest extent permitted by law, Aitemaad Maid Services is not
          liable for any indirect, incidental, or consequential damages arising
          from your use of our Services.
        </p>

        <h3 className="terms-of-service__subsection-title">7. Changes to These Terms</h3>
        <p className="terms-of-service__paragraph">
          We reserve the right to update or modify these Terms at any time. The
          latest version will always be available on this page. Your continued
          use of our Services indicates your acceptance of these updates.
        </p>

        <h3 className="terms-of-service__subsection-title">8. Governing Law</h3>
        <p className="terms-of-service__paragraph">
          These Terms are governed by the laws of [State/Country]. Any disputes
          will be resolved through binding arbitration in accordance with the
          rules of [Arbitration Association].
        </p>

        <h3 className="terms-of-service__subsection-title">9. Contact Us</h3>
        <p className="terms-of-service__paragraph">
          For any questions or concerns regarding these Terms, please reach out
          to us at:
        </p>
        <address>
          <strong>Email:</strong> support@aitemaad.com <br />
          <strong>Address:</strong> [Your Company Address]
        </address>
      </section>

      {show && (
        <button className="top__btn" onClick={scrollToTop}>
          Back to Top
        </button>
      )}
    </>
  );
}

export default TermsOfService;
