import React from 'react';
import '../styles/module.home.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { animateScroll as scroll } from 'react-scroll';

function Home() {
  const [show, setShow] = React.useState(false);
  
    React.useEffect(() => {
      const handleScroll = () => {
        if (window.scrollY > 200) {
          setShow(true);
        } else {
          setShow(false);
        }
      };
  
      window.addEventListener('scroll', handleScroll);
  
      return () => {
        window.removeEventListener('scroll', handleScroll);
      };
    }, []);
  
    const scrollToTop = () => {
      scroll.scrollToTop({ duration: 0 });
    };
  
    return (
    <>
    <header>
  <section className="header-content">
    <figure className="header-img">
     <img src="/img2.jpeg" alt="Professional Maid Services in Pakistan"/>
    </figure>
    <div className="header-description " style={{marginLeft: '10px'}}>
      <h1 className="header-title">Professional Maid Services in Pakistan</h1>
      <p>
        Get trusted, reliable and professional maid services in Pakistan. Expert cleaning solutions tailored to fit your schedule and budget. Book now and experience the best of cleaning services in Pakistan.
      </p>
      <meta name="description" content="Get trusted, reliable and professional maid services in Pakistan. Expert cleaning solutions tailored to fit your schedule and budget. Book now and experience the best of cleaning services in Pakistan." />
      <a href='/services/allservices'><button className="type-button">Learn More</button></a>
    </div>
  </section>
</header>

    <section className="services d-flex flex-wrap justify-content-center border rounded p-3" id="services">
      <div className="service-header container">
        <h2>Our Services</h2>
        <meta name="keywords" content="maid services, babysitter services, patient care, cook/chef services, helper services" />
        <p>
          Looking for the best maid services in Pakistan? Your Home Friend is a 
          trusted nationwide provider of professional, reliable,
           and top-quality domestic help. Our maid services are designed 
           to provide you with a clean and comfortable living space. Book 
           your appointment now and experience the best of cleaning services in Pakistan.
        </p>
      </div>
      <div className="service-list d-flex flex-wrap justify-content-center">
        <div className="service-item col-sm-3 m-3 border rounded p-3">
          <img src="/services (1).png" className="service-img rounded" alt="Maid Services in Pakistan"/>
          <h3 className="mt-3">Maid Services</h3>
          <p>
            Our professional and reliable maid services ensure a spotless home.
            Book your appointment now and let us take care of the rest! 
          </p>
          <a href="/services/maidservices" className="btn w-100 " >Learn More</a>
        </div>
        <div className="service-item col-sm-3 m-3 border rounded p-3">
          <img src="/services (6).png" className="service-img rounded" alt="Babysitter Services in Pakistan"/>
          <h3 className="mt-3">Babysitter Services</h3>
          <p>
            Our trusted and caring babysitter services ensure your child is in
            safe and nurturing hands. Schedule your babysitting session today!
          </p>
          <a href="/services/babysitter" className="btn w-100 ">Learn More</a>
        </div>
        <div className="service-item col-sm-3 m-3 border rounded p-3">
          <img src="/services (5).png" className="service-img rounded" alt="Patient care Services in Pakistan"/>
          <h3 className="mt-3">Patient care</h3>
          <p>
            Our compassionate patient care services ensure your
            loved ones receive the best care and support in the
            comfort of their own home.
          </p>
          <a href="/services/patientcare" className="btn w-100 ">Learn More</a>
        </div>
        <div className="service-item col-sm-3 m-3 border rounded p-3">
          <img src="/services (4).png" className="service-img rounded" alt="Cook / Chef Services in Pakistan"/>
          <h3 className="mt-3">Cook Services</h3>
          <p>
            Our professional and delicious meal preparation services are
            tailored to your specific needs and preferences. Book your cooking
            session today!
          </p>
          <a href="/services/cook" className="btn w-100 ">Learn More</a>
        </div>
        <div className="service-item col-sm-3 m-3 border rounded p-3">
          <img src="/services (3).png" className="service-img rounded" alt="Helper Services in Pakistan"/>
          <h3 className="mt-3">Helper Services</h3>
          <p>
            Our reliable and efficient helper services assist with household
            tasks, ensuring a smooth and stress-free experience. Book your
            helper session today!
          </p>
          <a href="/services/helpers" className="btn w-100 ">Learn More</a>
        </div>
      </div>
    </section>

    <section className="contact-info container-fluid d-flex align-items-center justify-content-center" style={{height:'auto', backgroundImage: 'url(/img3.jpeg)', backgroundSize: 'cover', backgroundPosition: 'center', backgroundRepeat: 'no-repeat', padding: '20px 0'}}>
      <div className="contact-info-content p-3 p-md-5" style={{backgroundColor: 'rgba(255,255,255,0.9)', borderRadius: '10px', width: '100%', maxWidth: '1200px'}}>
        <div className="row contact-info-list justify-content-center">
          <div className="col-12 col-md-4 contact-info-item text-center m-3">
            <a href="tel:+923069111004" style={{textDecoration: 'none', color: '#333'}} >
              <i className="bi bi-telephone-fill mb-2" style={{fontSize: '24px', color: '#fb826a'}}></i>
              <h3 className="mb-3" style={{color: '#333'}}>Phone No</h3>
              <p className="mb-3" style={{color: '#555'}}>+92 306 911 1004</p>
            </a>
          </div>
          <div className="col-12 col-md-4 contact-info-item text-center m-3">
            <a href="mailto:info@aitemaadmaidservices.com" style={{textDecoration: 'none', color: '#333'}} >
              <i className="bi bi-envelope-fill mb-2" style={{fontSize: '24px', color: '#fb826a'}}></i>
              <h3 className="mb-3" style={{color: '#333'}}>Email</h3>
              <p className="mb-3" style={{color: '#555'}}>info@aitemaadmaidservices.com</p>  
            </a>
          </div>
          <div className="col-12 col-md-4 contact-info-item text-center m-3">
            <a href='https://g.co/kgs/jQQf4h8'  style={{textDecoration: 'none', color: '#333'}} >
              <i className="bi bi-geo-alt-fill mb-2" style={{fontSize: '24px', color: '#fb826a'}}></i>
              <h3 className="mb-3" style={{color: '#333'}}>Address</h3>
              <p className="mb-3" style={{color: '#555'}}>Aitemaad House Maid Services, Opposite Street 18 main service road west F11/2 Islamabad, Pakistan.</p>
            </a>
          </div>
        </div>
      </div>
    </section>

      <section className='social d-flex justify-content-center align-items-center flex-wrap' style={{gap: '20px', padding: '20px 0'}}>
        <a href="https://www.facebook.com/aitemaadhousemaidservices/" target="_blank" rel="noopener noreferrer" className='social-link facebook d-flex justify-content-center align-items-center' style={{backgroundColor: '#3b5998', height: '60px', width: '60px', borderRadius: '50%', color: '#fff', fontSize: '24px', pointerEvents: 'none'}}><i className="bi bi-facebook"></i></a>
        <a href="https://twitter.com/aitemaadhouse" target="_blank" rel="noopener noreferrer" className='social-link twitter d-flex justify-content-center align-items-center' style={{backgroundColor: '#0077b6', height: '60px', width: '60px', borderRadius: '50%', color: '#fff', fontSize: '24px', pointerEvents: 'none'}}><i className="bi bi-twitter"></i></a>
        <a href="https://www.instagram.com/aitemaadhousemaidservices/" target="_blank" rel="noopener noreferrer" className='social-link instagram d-flex justify-content-center align-items-center' style={{backgroundColor: '#c13584', height: '60px', width: '60px', borderRadius: '50%', color: '#fff', fontSize: '24px', pointerEvents: 'none'}}><i className="bi bi-instagram"></i></a>
        <a href="https://www.linkedin.com/company/aitemaad-house-maid-services" target="_blank" rel="noopener noreferrer" className='social-link linkedin d-flex justify-content-center align-items-center' style={{backgroundColor: '#0288d1', height: '60px', width: '60px', borderRadius: '50%', color: '#fff', fontSize: '24px', pointerEvents: 'none'}}><i className="bi bi-linkedin"></i></a>
        <a href="https://www.youtube.com/channel/UC2dV4XtFqF7qG9YVXfJpQVA" target="_blank" rel="noopener noreferrer" className='social-link youtube d-flex justify-content-center align-items-center' style={{backgroundColor: '#ff0000', height: '60px', width: '60px', borderRadius: '50%', color: '#fff', fontSize: '24px', pointerEvents: 'none'}}><i className="bi bi-youtube"></i></a>
        <a href="https://wa.me/+923069111004" target="_blank" rel="noopener noreferrer" className='social-link whatsapp d-flex justify-content-center align-items-center' style={{backgroundColor: '#25d366', height: '60px', width: '60px', borderRadius: '50%', color: '#fff', fontSize: '24px', pointerEvents: 'none'}}><i className="bi bi-whatsapp"></i></a>
        <a href="mailto:info@aitemaad.com" target="_blank" rel="noopener noreferrer" className='social-link email d-flex justify-content-center align-items-center' style={{backgroundColor: '#0097a7', height: '60px', width: '60px', borderRadius: '50%', color: '#fff', fontSize: '24px', pointerEvents: 'none'}}><i className="bi bi-envelope"></i></a>
        <a href="https://www.pinterest.com/aitemaadhousemaidservices/" target="_blank" rel="noopener noreferrer" className='social-link pinterest d-flex justify-content-center align-items-center' style={{backgroundColor: '#bd081c', height: '60px', width: '60px', borderRadius: '50%', color: '#fff', fontSize: '24px', pointerEvents: 'none'}}><i className="bi bi-pinterest"></i></a>
        <a href="https://www.tiktok.com/@aitemaadhousemaidservices" target="_blank" rel="noopener noreferrer" className='social-link tiktok d-flex justify-content-center align-items-center' style={{backgroundColor: '#e53935', height: '60px', width: '60px', borderRadius: '50%', color: '#fff', fontSize: '24px', pointerEvents: 'none'}}><i className="bi bi-tiktok"></i></a>
      </section>


    <section className="about-us container-fluid d-flex align-items-center justify-content-center" style={{height:'70vh',backgroundImage: 'url(/img2.jpeg)',backgroundSize: 'cover',backgroundPosition: 'center',backgroundRepeat: 'no-repeat'}}>
      <div className="about-us-content p-5" style={{backgroundColor: 'rgba(255,255,255,0.9)', borderRadius: '10px', width: '100%', maxWidth: '600px'}}>
        <h2 className="mb-3" style={{color: '#333'}}>About Us</h2>
        <p className="mb-3" style={{color: '#444'}}>
          Looking for top-notch home maid services? Your Home Friend offers professional maid services, reliable babysitter services, compassionate patient care, skilled cook/chef services, and efficient helper services. Our commitment is to deliver exceptional experiences, ensuring complete satisfaction with our top-rated services.
        </p>
        <button className="btn w-100" style={{backgroundColor:'#fb826a', padding: '10px 20px'}}>Learn More</button>
      </div>
    </section>

    <section className="contact-us container-fluid d-flex align-items-center justify-content-center" style={{height:'100vh',backgroundSize: 'cover',backgroundPosition: 'center',backgroundRepeat: 'no-repeat'}}>
      <div className="contact-us-content  p-5 d-flex flex-column flex-md-row" style={{backgroundColor: 'rgba(255,255,255,0.9)', borderRadius: '10px', width: '100%', maxWidth: '1200px'}}>
        <div className="contact-us-form w-100 w-md-50 me-md-4 mb-4 mb-md-0">
          <h2 className="mb-3" style={{color: '#333'}}>Get in Touch</h2>
          <p className="mb-3" style={{color: '#444'}}>
            Have a question or need more information? Feel free to get in touch with us by filling out the form, emailing us, or calling us directly.
          </p>
          <form>
            <div className="mb-3">
              <input type="text" className="form-control" placeholder="Your Name" />
            </div>
            <div className="mb-3">
              <input type="email" className="form-control" placeholder="Your Email" />
            </div>
            <div className="mb-3">
              <textarea className="form-control" placeholder="Your Message" style={{height: '150px'}}></textarea>
            </div>
            <button type="submit" className="btn w-100" style={{backgroundColor:'#fb826a', padding: '10px 20px'}}>Send Message</button>
          </form>
        </div>
        <div className="contact-us-map w-100 w-md-50">
          <iframe title="Aitemaad House Maid Services Location" frameborder="0" scrolling="no" marginheight="0" marginwidth="0" src="https://maps.google.com/maps?width=600&amp;height=400&amp;hl=en&amp;q=18 Service Rd W, Golra F 11/2 F-11, Islamabad, Islamabad Capital Territory 45200&amp;t=&amp;z=14&amp;ie=UTF8&amp;iwloc=B&amp;output=embed"  width="100%" height="100%" style={{border: '0', borderRadius: '10px'}} allowFullScreen="" loading="lazy"></iframe>
        </div>
      </div>
    </section>

    {show && (
        <button className="top__btn" onClick={scrollToTop}>
          Back to Top
        </button>
      )}

    </>
  );
}

export default Home;

