import React from 'react';
import { animateScroll as scroll } from 'react-scroll';
import '../../styles/module.maid.css';
import { Helmet } from 'react-helmet';

function Babysitter() {
  const [show, setShow] = React.useState(false);

  React.useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 200) {
        setShow(true);
      } else {
        setShow(false);
      }
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const scrollToTop = () => {
    scroll.scrollToTop({duration: 0});
  };

  return (
    <>
    <Helmet>
      <title>Babysitter Services - Aitemaad Maid Services | Best Babysitter Services</title>
      <meta
        name="description"
        content="Aitemaad Maid Services offers professional babysitter services in Islamabad, Pakistan. Learn more about our babysitter services and how we can help you."
      />
      <meta
        name="keywords"
        content="maid services,Aitemaad House Maid Services, cleaning services, professional cleaning, Islamabad, house cleaning, office cleaning, deep cleaning, home cleaning, house maid services"
      />
    </Helmet>
     <section className="hero">
      <div className="hero__content">
        <h1 className="hero__title">Babysitter Services</h1>
        <p className="hero__subtitle">
          Expert babysitter services tailored to fit your schedule and budget. Book now!
        </p>
        <a className="hero__button" href='/about' style={{textDecoration: 'none'}}>
          Learn More
        </a>
      </div>
    </section>
    
    <section className="services container-fluid d-flex flex-wrap justify-content-center pt-5 pb-5" style={{height:'auto',backgroundSize: 'cover',backgroundPosition: 'center',backgroundRepeat: 'no-repeat'}}>
      <div className="service-card p-5 m-3 rounded border" style={{backgroundColor: 'rgba(255,255,255,0.9)', borderRadius: '10px', width: '100%', maxWidth: '400px'}}>
        <img src="/services (6).png" alt="babysitter services" className="img-fluid mb-3 rounded border" style={{width:'100%',height:'auto'}}/>
        <h3 className="mb-3">Monthly Basis Babysitter</h3>
        <p className="mb-3">
          We can provide a full day babysitter for your child. Our full day babysitter services are designed to provide a high level of care and attention to ensure a happy and healthy environment for your child.
        </p>
        <a className="btn w-100" href="/contact">Book Now</a>        
      </div>
      <div className="service-card p-5 m-3 rounded border" style={{backgroundColor: 'rgba(255,255,255,0.9)', borderRadius: '10px', width: '100%', maxWidth: '400px'}}>
        <img src="/services (6).png" alt="babysitter services" className="img-fluid mb-3 rounded border" style={{width:'100%',height:'auto'}}/>
        <h3 className="mb-3">One Day Babysitter</h3>
        <p className="mb-3">
          We can provide a half day babysitter for your child. Our half day babysitter services are designed to provide a high level of care and attention to ensure a happy and healthy environment for your child.
        </p>
        <a className="btn w-100" href="/contact">Book Now</a>
      </div>
    </section>

    {show && (
        <button className="top__btn" onClick={scrollToTop}>
          Back to Top
        </button>
      )}

    </>
  );
}

export default Babysitter;

