import React from 'react';
import '../styles/module.about.css';
import { animateScroll as scroll } from 'react-scroll';
import { Helmet } from 'react-helmet';

function About() {
  const [show, setShow] = React.useState(false);
    
      React.useEffect(() => {
        const handleScroll = () => {
          if (window.scrollY > 200) {
            setShow(true);
          } else {
            setShow(false);
          }
        };
    
        window.addEventListener('scroll', handleScroll);
    
        return () => {
          window.removeEventListener('scroll', handleScroll);
        };
      }, []);
    
      const scrollToTop = () => {
        scroll.scrollToTop({ duration: 0 });
      };
    
      return (
    <>
    <Helmet>
      <title>About Us - Aitemaad Maid Services | Islamabad's Best House Cleaning Services</title>
      <meta
        name="description"
        content="Aitemaad Maid Services is a professional house cleaning service based in Islamabad, Pakistan. Learn more about our company and services."
      />
      <meta property="og:title" content="Aitemaad Maid Services - About Us" />
      <meta property="og:url" content="https://aitemaad.com/about/" />
      <meta
        property="og:description"
        content="Aitemaad Maid Services is a professional house cleaning service based in Islamabad, Pakistan. Learn more about our company and services."
      />
      <meta property="og:type" content="website" />
    </Helmet>
    <section className="hero">
      <div className="hero__content">
        <h1 className="hero__title">Welcome to Our Company</h1>
        <p className="hero__subtitle">We provide the best services to make your life easier.</p>
        <a href='/contact' className="hero__button" style={{textDecoration: 'none'}}>
          Learn More
        </a>
      </div>
    </section>

    <section className="amazing">
      <div className="amazing__content">
        <h2 className="amazing__title">Why Choose Us?</h2>
        <p className="amazing__description">
          Our company excels in providing exceptional services, focusing on excellence, meticulous attention to detail, and unmatched customer satisfaction. We are committed to delivering top-notch quality services, ensuring client satisfaction and peace of mind.
        </p>
        <ul className="amazing__list">
          <li>Experienced Professionals</li>
          <li>Reliable and Trustworthy</li>
          <li>Customized Solutions</li>
          <li>Competitive Pricing</li>
          <li>Customer Satisfaction Guaranteed</li>
        </ul>
      </div>
    </section>

    <section className="amazing bg-white">
      <div className="container">
        <h2 className="amazing__title">Privacy Policy <i className="bi bi-shield-lock"></i></h2>
        <p className="amazing__description">
          At Aitemaad House Maid Services, we respect your privacy in accordance with the laws of Pakistan. We will not share your personal information with any third party unless it is necessary to provide you with the best services possible. We comply with the Personal Data Protection Act 2010 and the General Data Protection Regulation (GDPR) of the European Union.
        </p>
        <ul className="amazing__list">
          <li><i className="bi bi-check-circle-fill"></i> Your personal information is secure with us.</li>
          <li><i className="bi bi-check-circle-fill"></i> We will not share your personal information with any third party without your consent.</li>
          <li><i className="bi bi-check-circle-fill"></i> We will only use your information to provide you with the best services possible.</li>
        </ul>
        <a href="/privacy-policy" className="btn btn-outline">Learn More</a>
      </div>
    </section>

    <section className="amazing bg-light">
      <div className="container">
        <h2 className="amazing__title">Terms of Service <i className="bi bi-file-text"></i></h2>
        <p className="amazing__description">
          By using our services, you agree to comply with our terms and conditions. We reserve the right to modify these terms at any time. It is your responsibility to review them periodically.
        </p>
        <ul className="amazing__list">
          <li><i className="bi bi-check-circle-fill"></i> Use of our services is subject to applicable laws and regulations.</li>
          <li><i className="bi bi-check-circle-fill"></i> You agree not to misuse our services.</li>
          <li><i className="bi bi-check-circle-fill"></i> We are not liable for any damages arising from the use of our services.</li>
          <li><i className="bi bi-check-circle-fill"></i> We may terminate or suspend access to our services without prior notice.</li>
        </ul>
        <a href="/terms-of-service" className="btn btn-outline">Learn More</a>
      </div>
    </section>


    {show && (
        <button className="top__btn" onClick={scrollToTop}>
          Back to Top
        </button>
      )}
    </>
        
     
  );
}

export default About;

