import React from 'react';
import { animateScroll as scroll } from 'react-scroll';
import '../styles/module.privacy.css';

function PrivacyPolicy() {
  const [show, setShow] = React.useState(false);

  React.useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 200) {
        setShow(true);
      } else {
        setShow(false);
      }
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const scrollToTop = () => {
    scroll.scrollToTop({ duration: 300 });
  };

  return (
    <>
      {/* Hero Section */}
      <section className="hero maid-services">
        <div className="hero__content maid-services__content">
          <h1 className="hero__title maid-services__title">Your Privacy Matters</h1>
          <p className="hero__subtitle maid-services__subtitle">
            We're committed to protecting your personal information with the highest level of care and transparency. Learn how we safeguard your data and respect your trust.
          </p>
        </div>
      </section>

      {/* Privacy Policy Section */}
      <section className="privacy-policy container">
        <div className="row">
          <div className="col-lg-6 mb-4">
            <h2 className="privacy-policy__title"><i className="bi bi-shield-lock"></i> Our Commitment to Your Privacy</h2>
            <p className="privacy-policy__paragraph">
              At <strong>Aitemaad Maid Services</strong>, your privacy is of utmost importance to us. This policy outlines how we collect, use, and protect your personal data while ensuring transparency and security.
            </p>
          </div>

          <div className="col-lg-6 mb-4">
            {/* Data Collection */}
            <h3 className="privacy-policy__subsection-title"><i className="bi bi-person-check"></i> Data We Collect</h3>
            <p className="privacy-policy__paragraph">
              To deliver our services efficiently, we may collect the following personal data:
            </p>
            <ul className="privacy-policy__list">
              <li><i className="bi bi-envelope"></i> Name, email address, and phone number for communication</li>
              <li><i className="bi bi-house-door"></i> Address details for scheduling services</li>
              <li><i className="bi bi-chat-left-text"></i> Feedback and reviews to improve our offerings</li>
            </ul>
          </div>

          <div className="col-lg-6 mb-4">
            {/* Data Usage */}
            <h3 className="privacy-policy__subsection-title"><i className="bi bi-gear"></i> How We Use Your Data</h3>
            <p className="privacy-policy__paragraph">
              Your personal information is used for:
            </p>
            <ul className="privacy-policy__list">
              <li><i className="bi bi-calendar-check"></i> Scheduling and managing appointments</li>
              <li><i className="bi bi-person-headset"></i> Offering personalized customer support</li>
              <li><i className="bi bi-wallet2"></i> Secure payment processing</li>
              <li><i className="bi bi-bar-chart"></i> Enhancing service quality</li>
              <li><i className="bi bi-shield-check"></i> Meeting legal and regulatory requirements</li>
            </ul>
          </div>

          <div className="col-lg-6 mb-4">
            {/* Data Protection */}
            <h3 className="privacy-policy__subsection-title"><i className="bi bi-lock"></i> Data Protection Measures</h3>
            <p className="privacy-policy__paragraph">
              To safeguard your data, we employ:
            </p>
            <ul className="privacy-policy__list">
              <li><i className="bi bi-shield-fill"></i> End-to-end encryption for sensitive data</li>
              <li><i className="bi bi-credit-card-2-front"></i> Secure payment gateways</li>
              <li><i className="bi bi-search"></i> Regular system audits</li>
              <li><i className="bi bi-key"></i> Access control for authorized personnel only</li>
            </ul>
          </div>

          <div className="col-lg-6 mb-4">
            {/* User Rights */}
            <h3 className="privacy-policy__subsection-title"><i className="bi bi-person-badge"></i> Your Rights</h3>
            <p className="privacy-policy__paragraph">
              As our valued user, you have the following rights:
            </p>
            <ul className="privacy-policy__list">
              <li><i className="bi bi-unlock"></i> Access, update, or delete your personal information</li>
              <li><i className="bi bi-file-earmark-text"></i> Request a copy of your stored data</li>
              <li><i className="bi bi-x-circle"></i> Withdraw consent to data processing</li>
              <li><i className="bi bi-exclamation-circle"></i> File a complaint with relevant authorities</li>
            </ul>
          </div>

          <div className="col-lg-6 mb-4">
            {/* Policy Updates */}
            <h3 className="privacy-policy__subsection-title"><i className="bi bi-arrow-repeat"></i> Policy Updates</h3>
            <p className="privacy-policy__paragraph">
              This policy may be updated periodically to align with legal or business changes. Updates will be reflected on this page, and significant changes will be communicated to users directly.
            </p>

            {/* Contact Information */}
            <h3 className="privacy-policy__subsection-title"><i className="bi bi-envelope-open"></i> Contact Us</h3>
            <p className="privacy-policy__paragraph">
              If you have any questions or concerns, reach out to us:
            </p>
            <ul className="privacy-policy__list">
              <li><i className="bi bi-envelope-fill"></i> Email: <a href="mailto:support@Aitemaadmaidservices.com">support@Aitemaadmaidservices.com</a></li>
              <li><i className="bi bi-telephone-fill"></i> Phone: +92 306 9111 004</li>
              <li><i className="bi bi-geo-alt-fill"></i> Address: 18 Service Rd W, Golra F 11/2 F-11, Islamabad, Pakistan</li>
            </ul>
          </div>
        </div>
      </section>

      {/* Back to Top Button */}
      {show && (
        <button className="top__btn" onClick={scrollToTop}>
          Back to Top
        </button>
      )}
    </>
  );
}

export default PrivacyPolicy;
